body {
  margin: 0;
  padding: 0;
}

.stopwatch{
  justify-content: center;
  align-items: center;
  height: 80vmin;
  width: 80vmin;
  border: 1vmin dotted #000;
  border-radius: 50%;
  margin: 0 auto;
  position: relative;

  &__minutes {
    height: 20vmin;
    width: 3vmin;
    background-color: #0700ff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    transform-origin: bottom;
    animation: move 3600s infinite linear;
  }

  &__seconds {
    height: 38vmin;
    width: 1.5vmin;
    background-color: #2c8000;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-100%, -100%);
    animation: move 60s infinite linear;
    transform-origin: bottom;
    }


&__center {
  width: 5vmin;
  height: 5vmin;
  border-radius: 50%;
  background-color: #f6a603;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: bottom center;

  }

  &--speed-up {
    .stopwatch {
      .stopwatch__seconds {
        animation-duration: 10s;
      }
    }
  }

  &--speed-up {
    .stopwatch {
      .stopwatch__minutes {
        animation-duration: 6000s;
      }
    }
  }
  }


@keyframes move {
  0% {
    transform: translate(-50%, -100%) rotate(0deg);
  }
  100% {
  transform: translate(-50%, -100%)  rotate(360deg);
}
}


